body{
  background-color: #171717;
  color: #ffffff;
  font-family: 'Titillium Web',sans-serif;
}

.App{
  text-align: center;
  padding: 20px;
}

.content-grid{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 10px;
}

.content-grid img{
  width: 100%;
  aspect-ratio: 2 / 3;
}

.search-bar{
  margin-bottom: 20px;
}